import React, { useState, useEffect } from "react";
import DataTable from "react-data-table-component";
import TopBar from "../../../components/Users/TopBar";
import ModuleSideNav from "../../../components/Users/ModuleSideNav.jsx";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  SectionWrapper,
  Wrapper,
} from "../../../components/Styles/MainHeadStyles.js";
import { DataRoomSection } from "../../../components/Styles/DataRoomStyle.js";
import axios from "axios";
import { FaEye, FaEdit, FaLock, FaUnlock } from "react-icons/fa"; // FontAwesome icons
import DangerAlertPopup from "../../../components/Admin/DangerAlertPopup";
import { Link } from "react-router-dom";
import CheckApprovalButton from "../../../components/Users/CheckApprovalButton.jsx";
import { useNavigate } from "react-router-dom";
export default function RecordRoundList() {
  const navigate = useNavigate();
  const storedUsername = localStorage.getItem("SignatoryLoginData");
  const userLogin = JSON.parse(storedUsername);
  const [records, setrecords] = useState([]);
  const [authorizedData, setAuthorizedData] = useState(null);
  const [messagesuccessError, setmessagesuccessError] = useState("");
  const [errr, seterrr] = useState(false);
  const [dangerMessage, setdangerMessage] = useState("");
  var apiURLInvestor = "http://blueprintcatalyst.com/api/user/investor/";
  const apiURLSignature = "http://blueprintcatalyst.com/api/user/";
  const [LockId, setLockId] = useState("");
  document.title = "Investment Rounds Overview";
  const customStyles = {
    table: {
      style: {
        border: "1px solid #dee2e6",
        borderRadius: "12px",
        overflow: "auto",
      },
    },
    headCells: {
      style: {
        backgroundColor: "#efefef",
        fontWeight: "600",
        fontSize: "0.8rem",
        color: "#000",
        textTransform: "uppercase",
        whiteSpace: "nowrap",
      },
    },
    cells: {
      style: {
        whiteSpace: "nowrap",
        overflow: "hidden",
        textOverflow: "ellipsis",
      },
    },
    rows: {
      style: {
        fontSize: "0.8rem",
        fontWeight: "500",
      },
      stripedStyle: {
        backgroundColor: "#fff",
      },
    },
    pagination: {
      style: {
        marginTop: "15px",
        backgroundColor: "#fafafa",
        padding: "12px 16px",
      },
    },
  };
  useEffect(() => {
    getAuthorizedSignature();
  }, []);
  const getAuthorizedSignature = async () => {
    let formData = {
      company_id: userLogin.companies[0].id,
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(
        apiURLSignature + "getAuthorizedSignature",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      const checkData = res.data.results;
      if (checkData.length > 0) {
        setAuthorizedData(checkData[0]);
      }
    } catch (err) {}
  };
  useEffect(() => {
    getreports();
  }, []);

  const getreports = async () => {
    const formData = {
      company_id: userLogin.companies[0].id,
    };
    try {
      const generateRes = await axios.post(
        apiURLInvestor + "getrecordRoundList",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );

      setrecords(generateRes.data.results);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };

  // Toggle a row

  // Select/Deselect all

  const columns = [
    {
      name: "Share Class (Name of Round)",
      selector: (row) => row.shareClassType + " " + row.nameOfRound,
      sortable: true,
    },
    {
      name: "Target Raise Amount",
      selector: (row) => {
        const amount = row.roundsize ? Number(row.roundsize) : 0;
        const currency = row.currency || ""; // e.g., â‚¹ or $
        const formattedAmount = amount.toLocaleString("en-IN", {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2,
        });
        return `${currency} ${formattedAmount}`;
      },
      sortable: true,
    },
    {
      name: "Number of Shares",
      selector: (row) => {
        const formattedAmount = Number(row.issuedshares).toLocaleString(
          "en-IN",
          {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2,
          }
        );
        return `${formattedAmount}`;
      },
      sortable: true,
    },

    {
      name: "Status of Round",
      selector: (row) => row.dateroundclosed,
      sortable: true,
      cell: (row) => {
        const isActive = row.roundStatus === "ACTIVE";
        const displayText = isActive
          ? "ACTIVE"
          : `CLOSED: ${formatCurrentDate(row.dateroundclosed)}`;

        return (
          <span
            style={{
              padding: "4px 12px",
              borderRadius: "12px",
              fontWeight: "600",
              color: isActive ? "#065f46" : "#b91c1c", // green or red text
              backgroundColor: isActive ? "#d1fae5" : "#fee2e2", // green or red bg
              fontSize: "12px",
              display: "inline-block",
            }}
          >
            {displayText}
          </span>
        );
      },
    },
    {
      name: "Action",
      cell: (row) => {
        // Check if the signature is approved
        const isApproved = authorizedData?.approve === "Yes";

        if (!isApproved) {
          // If not approved, show a disabled message
          return (
            <span
              style={{
                padding: "4px 12px",
                borderRadius: "12px",
                fontWeight: "600",
                color: "#b91c1c", // green or red text
                backgroundColor: "#fee2e2", // green or red bg
                fontSize: "12px",
                display: "inline-block",
              }}
            >
              Pending Approval
            </span>
          );
        }

        // If approved, show the normal action buttons
        const isClosed = row.roundStatus === "ACTIVE";

        return (
          <div className="d-flex gap-2">
            {/* Show Edit only if round is ACTIVE */}
            {isClosed && (
              <Link
                to={`/edit-record-round/${row.id}`}
                title="View Record Round"
                className="icon_btn green_clr"
              >
                <FaEdit /> Edit
              </Link>
            )}

            {row.is_locked === "Yes" || row.is_shared === "Yes" ? (
              <button className="icon_btn red_clr" title="Locked" disabled>
                <FaLock /> Locked
              </button>
            ) : (
              <button
                className="icon_btn red_clr"
                onClick={() => handlelockreport(row.id)}
                title="Lock The Document For Sharing"
              >
                <FaUnlock /> Unlock
              </button>
            )}
          </div>
        );
      },
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
      width: "200px",
    },
  ];
  const handlelockreport = async (Id) => {
    setLockId(Id);
    setdangerMessage(
      "Are you sure? Once locked, this document cannot be unlocked."
    );
  };
  const handleConfirm = async () => {
    const formData = {
      company_id: userLogin.companies[0].id,
      lock_id: LockId,
    };
    console.log(formData);
    try {
      const generateRes = await axios.post(
        apiURLInvestor + "recordRoundLock",
        formData,
        {
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      setdangerMessage("");
      setmessagesuccessError(generateRes.data.message);
      getreports();
      setTimeout(() => {
        setmessagesuccessError("");
      }, 3500);
    } catch (err) {
      console.error("Error generating summary", err);
    }
  };
  const conditionalRowStyles = [
    {
      when: (row) => true, // apply to all rows
      style: {
        "&:hover": {
          backgroundColor: "var(--lightRed)", // apna hover color
        },
      },
    },
  ];

  const [searchText, setSearchText] = useState("");

  // Filter data by nameofreport (case insensitive)
  const filteredData = records.filter((item) => {
    const text = (searchText || "").toLowerCase();
    return (
      (item.shareClassType || "").toLowerCase().includes(text) ||
      (item.nameOfRound || "").toLowerCase().includes(text) ||
      (item.currency || "").toLowerCase().includes(text) ||
      (item.issuedshares || "").toLowerCase().includes(text)
    );
  });

  function formatCurrentDate(input) {
    const date = new Date(input);

    if (isNaN(date)) return "";
    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();

    const getOrdinal = (n) => {
      if (n >= 11 && n <= 13) return "th";
      switch (n % 10) {
        case 1:
          return "st";
        case 2:
          return "nd";
        case 3:
          return "rd";
        default:
          return "th";
      }
    };

    return `${month} ${day}${getOrdinal(day)}, ${year}`;
  }
  const [isCollapsed, setIsCollapsed] = useState(false);
  //Share Report

  return (
    <>
      <>
        <Wrapper>
          <div className="fullpage d-block">
            <div className="d-flex align-items-start gap-0">
              <ModuleSideNav
                isCollapsed={isCollapsed}
                setIsCollapsed={setIsCollapsed}
              />
              <div
                className={`global_view ${
                  isCollapsed ? "global_view_col" : ""
                }`}
              >
                <TopBar />
                <SectionWrapper className="d-block p-md-4 p-3">
                  <div className="container-fluid">
                    {messagesuccessError && (
                      <div
                        className={`flex items-center justify-between gap-3 shadow-lg ${
                          errr ? "error_pop" : "success_pop"
                        }`}
                      >
                        <div className="d-flex align-items-center gap-2">
                          <span className="d-block">{messagesuccessError}</span>
                        </div>

                        <button
                          type="button"
                          className="close_btnCros"
                          onClick={() => setmessagesuccessError("")}
                        >
                          Ã—
                        </button>
                      </div>
                    )}
                    {dangerMessage && (
                      <DangerAlertPopup
                        message={dangerMessage}
                        onConfirm={handleConfirm}
                        onCancel={() => {
                          setdangerMessage("");
                        }}
                      />
                    )}
                    <DataRoomSection className="d-flex flex-column gap-2">
                      <div className="titleroom d-flex justify-content-between align-items-center border-bottom pb-3">
                        {/* Heading on the left */}
                        <div className="pb-3 bar_design">
                          <h4 className="h5 mb-0">
                            Investment Rounds Overview
                          </h4>
                        </div>
                        {/* Buttons on the right */}
                        <div className="d-flex gap-2">
                          <CheckApprovalButton
                            authorizedData={authorizedData}
                            to="/createrecord"
                            className="generatebutton px-4 py-2 fn_size_sm btn btn-outline-dark active d-flex align-items-center gap-2"
                          >
                            Start New Funding Round
                          </CheckApprovalButton>
                        </div>
                      </div>

                      <div className="d-flex justify-content-end my-2 p-0">
                        <input
                          type="search"
                          placeholder="Search Here..."
                          className="form-control"
                          value={searchText}
                          onChange={(e) => setSearchText(e.target.value)}
                          style={{
                            padding: "10px 15px",
                            width: "100%",
                            maxWidth: "300px",
                            fontSize: "14px",
                            borderRadius: "10px",
                          }}
                        />
                      </div>
                      <div className="d-flex flex-column justify-content-between align-items-start tb-box">
                        <DataTable
                          customStyles={customStyles}
                          conditionalRowStyles={conditionalRowStyles}
                          columns={columns}
                          className="datatb-report"
                          data={filteredData}
                          pagination
                          highlightOnHover
                          striped
                          responsive
                        />
                      </div>
                    </DataRoomSection>
                  </div>
                </SectionWrapper>
              </div>
            </div>
          </div>
        </Wrapper>
      </>
    </>
  );
}
